"use client";

import { useState } from "react";

export default function Home() {
  const [code, setCode] = useState("");
  const [sections, setSections] = useState({
    output: "",
    assembly: "",
    machine: ""
  });
  const [selected, setSelected] = useState("output");

  const examples = {
    basic: `sugod
  ipakita: "Hi, Bisaya++";
  ipakita: "Bisaya++ Interpreter";
katapusan`,
    arithmetic: `sugod
  mugna numero b = 10;
  mugna numero c = 20;
  mugna numero a = b+c+c+b;
  ipakita: a;
katapusan		`,
    multiply: `sugod
  ipakita: 10*2;
katapusan`
  };

  // -----------------------------
  // RUN CODE
  // -----------------------------
  const runCode = async () => {
    if (!code.trim()) return;

    const res = await fetch("/api/parse", {
      method: "POST",
      body: code,
    });

    const data = await res.json();

    if (data.error) {
      setSections({
        output: data.error,
        assembly: "",
        machine: "",
      });
      return;
    }

    const text = data.output;

    const extract = (start, end) => {
      const s = text.indexOf(start);
      if (s === -1) return "";
      const e = text.indexOf(end, s + start.length);
      if (e === -1) return text.slice(s + start.length).trim();
      return text.slice(s + start.length, e).trim();
    };

    setSections({
      output: extract("=== STARTING COMPILATION ===", "=== MIPS64 ASSEMBLY CODE ==="),
      assembly: extract("=== MIPS64 ASSEMBLY CODE ===", "=== MACHINE CODE ==="),
      machine: extract("=== MACHINE CODE ===", "=== COMPILATION COMPLETE ==="),
    });

    setSelected("output");
  };

  // -----------------------------
  // CLEAR EVERYTHING
  // -----------------------------
  const clearAll = () => {
    setCode("");
    setSections({
      output: "",
      assembly: "",
      machine: "",
    });
    setSelected("output");
  };

  // -----------------------------
  // LOAD EXAMPLE
  // -----------------------------
  const loadExample = (name) => {
    setCode(examples[name]);
  };

  const displayed = sections[selected];

  return (
    <main className="min-h-screen bg-gray-900 text-white p-6">

      <h1 className="text-3xl font-bold mb-6">Bisdak Compiler IDE</h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">

        {/* -----------------------------
            CODE EDITOR PANEL
        ------------------------------ */}
        <div className="bg-gray-800 p-4 rounded-lg border border-gray-700">
          <h2 className="text-xl font-semibold mb-2">Code Editor</h2>

          <textarea
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="w-full h-80 bg-black text-green-400 p-3 rounded-lg font-mono text-sm"
            placeholder="Type your code here..."
          />

          {/* BUTTONS */}
          <div className="flex gap-4 mt-4">

            {/* RUN */}
            <button
              onClick={runCode}
              className="px-6 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg font-semibold"
            >
              RUN
            </button>

            {/* CLEAR */}
            <button
              onClick={clearAll}
              className="px-6 py-2 bg-red-600 hover:bg-red-700 rounded-lg font-semibold"
            >
              CLEAR
            </button>

            {/* EXAMPLES DROPDOWN */}
            <div className="relative group">
              <button className="px-6 py-2 bg-green-600 hover:bg-green-700 rounded-lg font-semibold">
                Examples â–¼
              </button>

              <div className="absolute left-0 mt-2 bg-gray-700 rounded-lg shadow-lg w-60 hidden group-hover:block">

                <button
                  onClick={() => loadExample("basic")}
                  className="block w-full text-left px-4 py-2 hover:bg-gray-600"
                >
                  Basic Example
                </button>

                <button
                  onClick={() => loadExample("arithmetic")}
                  className="block w-full text-left px-4 py-2 hover:bg-gray-600"
                >
                  Arithmetic Example
                </button>

                <button
                  onClick={() => loadExample("multiply")}
                  className="block w-full text-left px-4 py-2 hover:bg-gray-600"
                >
                  Multiply Example
                </button>

              </div>
            </div>

          </div>

        </div>

        {/* -----------------------------
            OUTPUT PANEL
        ------------------------------ */}
        <div className="bg-gray-800 p-4 rounded-lg border border-gray-700">
          <h2 className="text-xl font-semibold mb-2">Output</h2>

          {/* BUTTONS */}
          <div className="flex gap-4 mb-4">

            <button
              className={`px-4 py-2 rounded-lg ${selected === "output" ? "bg-blue-600" : "bg-gray-600"}`}
              onClick={() => setSelected("output")}
            >
              Output
            </button>

            <button
              className={`px-4 py-2 rounded-lg ${selected === "assembly" ? "bg-blue-600" : "bg-gray-600"}`}
              onClick={() => setSelected("assembly")}
            >
              Assembly
            </button>

            <button
              className={`px-4 py-2 rounded-lg ${selected === "machine" ? "bg-blue-600" : "bg-gray-600"}`}
              onClick={() => setSelected("machine")}
            >
              Machine Code
            </button>

          </div>

          <pre className="w-full h-80 bg-black p-3 overflow-auto text-yellow-300 font-mono text-sm">
            {displayed}
          </pre>

        </div>

      </div>
    </main>
  );
}
